/***** ERIC MEYER CSS RESET  *****/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after, q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}
/* END OF ERIC MEYER CSS RESET */
/*html {overflow-y: scroll;}*/ /*Removed by request of Peter Willert*/


/************************** BASE **************************/

* {
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0); /* Removes link highlight */
	-webkit-user-select: none; /* prevent copy paste and annoying ios magnifier */
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	-webkit-touch-callout: none; /* prevent link popup options */
	-webkit-text-size-adjust: none; /* This stops the iPhone from automatically changing the size of the text when you flip the phone */
	outline: none;
	background-repeat: no-repeat;
}

 /* Content that is hidden from sighted users but available to screen readers */
.visually-hidden {
	clip: rect(0 0 0 0);
	clip-path: inset(50%);
	height: 1px;
	overflow: hidden;
	position: absolute;
	white-space: nowrap;
	width: 1px;
}

/* Base RCF focus styles */
.activityContainer *:focus,
.referenceContentContainer *:focus {
	outline: 2px solid #000;
	outline-offset: -1px;
	z-index: 1;
}

.desktop .mobile {
    display: none;
}
.mobile .desktop {
    display: none;
}

input.text, textarea {
	-webkit-user-select: text;
	-khtml-user-select: text;
	-moz-user-select: text;
	-o-user-select: text;
	-ms-user-select: text;
	user-select: text;
}

* html .clearfix             { zoom: 1; } /* IE6 */
*:first-child+html .clearfix { zoom: 1; } /* IE7 */
/* new clearfix */
.clearfix:after {
	visibility: hidden;
	display: block;
	font-size: 0;
	content: " ";
	clear: both;
	height: 0;
}
input {
	margin: 0;
	padding: 0;
}
select {
	font-size: 1em;
}
body, input.text, select, textarea, input.typeinGroup {
	font-family: Helvetica, Arial, sans-serif;
}
body {
	background-color: #ddd;
	font-size: 14px;
	line-height: 1.75em;
}
textarea {
	font-size: 1em;
}

/* BASIC FORMATTING */
b, strong, .activity .phons {
	font-weight: bold;
}
i, em {
	font-style: italic;
}
.underline {
	text-decoration: underline;
}
sup {
	font-size: 0.8em;
	position: relative;
	bottom: 8px;
}
sub {
	font-size: 0.8em;
	position: relative;
	top:8px;
}

/* IMAGES */
img {
	max-width: 100%;
	display: block;
	margin-bottom: 0.875em;
	vertical-align: bottom; /*Without this, inline images display a space below the baseline, used by decenders like q, p, y, etc*/
}
.blockSet .block img {
	margin-right: 0;
	line-height: 1em;
	margin-bottom: 0.875em;
}
p img, li img, td img, h1 img, h2 img, h3 img {
	vertical-align: middle;
	display: inline;
	margin: 0;
}

/* LINKS */
a {
	color: #274175;
}
a.popupLink {
	color: #83A0B0;
	text-decoration: none;
	border-bottom: 1px solid #999;
	position: relative;
}
a.popupLink:hover {
	background-color: #ddd;
}

/* HEADINGS */
h1 {
	font-size: 2em;
	font-weight: bold;
	margin-bottom: 24px;
	line-height: 1em;
}
h2 {
	font-size: 1.5em;
	font-weight: bold;
	line-height: 1em;
	margin-bottom: 12px;
}
h3 {
	font-size: 1em;
	line-height: 1.75em;
	font-weight: bold;
	margin-bottom: 0.875em;
}
p {
	margin-bottom: 0.875em;
}
.rubric p {
	margin-bottom: 0;
}

/* TABLES */
table {
	margin-bottom: 1.75em;
}
td, th {
	border: 1px solid #FFF;
	padding: 0.7em 0.875em;
	text-align: left;
	vertical-align: top;
}
tr.even td {
	background-color: #eee;
}
tr.odd td {
	background-color: #e3e3e3;
}
th {
	font-weight: bold;
	background-color: #000;
	color: #FFF;
	padding: 0.4em 0.875em;
}
table.mm_fullWidth {
	width: 100%;
}

/* LISTS */
.content ul, .content ol {
	margin-bottom: 1em;
}
.content ul ul,.content ol ul,.content ol ol,.content ul ol {
	margin-bottom: 0;
}
.main ul, .rubric ul, .main ol, .rubric ol {
	padding-left: 1.5em;
}
.main ul li ul, .main ul li ol, .main ol li ul, .main ol li ol {
	padding-left: 2em;
}
ul.plain {
	padding-left: 0;
}
ul.plain > li {
	list-style-type: none;
}
ul.bullet > li {
	list-style-type: square;
}
ol.numbered > li {
	list-style-type: decimal;
	margin-bottom: 0.875em;
}
ol.numbered > li:last-child,
ol.numbered > li ul.plain > li,
ol.numbered > li ul.bullet > li,
ol.numbered > li ol.alpha > li {
	margin-bottom: 0;
}
ol.numbered.checkboxList > li,
ol.numbered.radioList > li {
	margin-bottom: 0;
}
ol.alpha > li {
	list-style-type: lower-alpha;
}
ol.upper-alpha > li {
	list-style-type: upper-alpha;
}
ol.roman > li {
	list-style-type: lower-roman;
}
ul.horizontal,
ol.horizontal {
	padding-left: 0;
}
ul.horizontal > li,
ol.horizontal > li {
	display: inline;
	margin-right: 1em;
}
ul.horizontal > li:last-child,
ol.horizontal > li:last-child {
	margin-right: 0;
}

ol > li.unnumbered {
	list-style-type: none;
}

li p {
	margin-bottom: 0;
}
.rubric ol > li, .rubric ul > li {
	margin-bottom: 0;
}
.rubric ol, .rubric ul {
	margin-bottom: 0;
}

.main ul.canDoList {
	padding-left: 0;
}


/************************** LAYOUT **************************/

.learningObjectHeadAndContent  {
	padding-left: 6px;
	padding-right: 6px;
}

/* SECTION */
.sectionHead {
	margin-bottom: 0.875em;
}

.activity,
.referenceContent {
	background-color: #FFF;
	border: 1px solid #000;
	padding: 0.875em;
	background-repeat: no-repeat;
	background-position: bottom right ;
	clear: both;

	--highlight-color1: rgba(41, 182, 246, 1);
	--highlight-color2: rgba(255, 0, 0, 1);
	--highlight-color3: rgba(255, 215, 0, 1);
	--highlight-color4: rgba(0, 128, 0, 1);

	--highlight-color1-light: rgba(41, 182, 246, 0.3);
	--highlight-color2-light: rgba(255, 0, 0, 0.3);
	--highlight-color3-light: rgba(255, 215, 0, 0.3);
	--highlight-color4-light: rgba(0, 128, 0, 0.3);

	--selection-color: #29b6f6;

	--freeDrawing-tools-background: #aaa;
	--freeDrawing-border: #404040;
	--freeDrawing-color1: #000000;
	--freeDrawing-color2: #EA3E32;
	--freeDrawing-color3: #89C656;
	--freeDrawing-color4: #3981BB;
	--freeDrawing-color5: #F7CB67;
}

/* RCF-11026 - activity audio player is now a real 'audio' element - we should hide it ! */
.activity div.activityAudioPlayerContainer {
	display: none;
}

/* RCF-10863 - ensure that the box-sizing of typein / text-inputs is always content-box */
.activity .typein input[type=text].text {
	box-sizing: content-box !important;
}

.activityContainer,
.referenceContentContainer {
	margin-bottom: 3.5em;
	color: #000;
}

/* overall score / open-gradable activities only */

.activity .overallScore {
	display: none;
}

.activity.reviewTeacherFeedback .overallScore {
	display: block;
    margin-top: 10px;
}

.activity .overallScore > p > [class*='score'] {
	display: block;
	padding: 5px;
}

.activity .overallScore .scoreLabel {
	background-color: #20a9c4;
}

.activity .overallScore .score {
	background-color: #eee;
}


.block .lastChild, .rubric .lastChild {
	margin-bottom: 0;
}
.activityHead {
	font-size: 1.5em;
	font-weight: bold;
	margin-bottom: 0.375em;
}

.rubricContainer {
	position: relative;
}

.rubric {
	background-color: #f8f8f8;
	color: #000;
	border: 1px solid #333;
	padding: 0.875em;
}
.actNum {
	float: left;
	margin-right: 1em;
}
.rubricBody {
	display: block;
}
.activity.unnumbered .rubricBody {
	margin-left: 0;
}
.main {
	position: relative;
	padding: 0.875em 0;
}
.unmarked .main {
	padding-bottom: 0;
}
.activity div.lastChild {
	margin-bottom: 0;
}
.block {
	margin-bottom: 0.875em;
}
.interactiveText {
	margin-bottom: 0.875em;
}
.blockSet > .block {
	padding: 0.875em;
	border: 1px solid #333;
	text-align: left;
}
.main div.lastChild {
	margin-bottom: 0;
}
.clear {
	clear: both;
}

/* MARKS */
.markable {
	position: relative;
}
.mark {
	display: none;
	height: 16px;
	width: 16px;
	background-repeat: no-repeat;
	position: absolute;
	z-index: 10;
	right: -1em;
	bottom: 0.3em;
}
.mark.wrong, .mark.correct {
	display: inline;
}
.typein .mark {
	top: -0.6em;
	right: -0.3em;
}
.dropDown .mark {
	top: -0.6em;
	right: -0.3em;
}
/*
.selectable .mark {
	top: -0.4em;
	right: -0.5em;
}*/
.interactiveText .selectableMarkContainer {
	position: relative;
}
.selectableMarkContainer .mark {
	position: absolute;
	left: -10px;
}
.category .mark {
	right: -1.5em;
}
.droppable .mark {
	top: -0.6em;
	right: -0.3em;
}
.correct {
	background-image: url(../images/tick.png);
}
.wrong {
	background-image: url(../images/cross.png);
}
@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min-device-pixel-ratio: 2) {
	.correct {
		background-image: url(../images/tick@2x.png);
		background-size: 16px;
	}
	.wrong {
		background-image: url(../images/cross@2x.png);
		background-size: 16px;
	}
}


/************************** MODULE **************************/

/*** MARKED COMPONENENTS ***/

.example, .mm_example {
	font-style: italic;
	color: #666;
}
select.example {
	font-style: normal;
}
input.text.example, .dragTarget.example, .mm_example {
	border-color: #ccc;
	color: #666;
}
input.text.example, .typeinGroup input.typeinGroup.example {
	border: 1px solid #ccc;
	color: #666;
}
.dropTarget .example {
	border: 0;
	background: 0;
}

/* DROPPABLE */

.stickyWordBox {
	position: -webkit-sticky;
	position: sticky;
}

.wordbox-position-default .stickyWordBox {
	top: 0;
}

.wordbox-position-top .stickyWordBox {
	top: 0;
}

.wordbox-position-bottom .stickyWordBox {
	bottom: 0;
}

.wordBox {
	background-color: #f8f8f8;
	border: 1px solid #333;
	margin-bottom: 0.875em;
	padding: 0.5em;
	padding-bottom: 0;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	border-radius: 5px;
}
.wordBox.dragOverDrop {
	background-color: #ccc;
}
.wordBox ul {
	padding-left: 0;
	margin-bottom: 0;
}
.wordBox li.dragItem,
.keyboardPlaceholder {
	display: inline-block;
	zoom: 1;
	*display: inline;
	padding-left: 0.4em;
	padding-right: 0.4em;
	vertical-align: top;
	border: 1px solid #333;
	background-color: #FFF;
	margin-right: 0.5em;
	margin-bottom: 0.5em;
	cursor: pointer;
	line-height: 1.5em;
	color: #000;
	-webkit-transition: 0.2s opacity;
	-moz-transition: 0.2s opacity;
	-ms-transition: 0.2s opacity;
	-o-transition: 0.2s opacity;
	transition: 0.2s opacity;
	opacity:1;
}

.category li.dragItem.dragged, .category li.dragged {
    opacity: 0.3;
}

.wordBox li.dragItem.dragged {
	visibility: hidden;
}


.dragTarget {
	display: inline-block;
	zoom: 1;
	*display: inline;
	min-width: 6em;
	line-height: 1.5em; /*Set to the text size*/
	padding-left: 0.4em;
	padding-right: 0.4em;
	cursor: pointer;
	border: 1px solid #333;
	background-color: #f8f8f8;
	margin-left: 0.1em;
	margin-right: 0.1em;
	position: relative; /* fix for chrome rendering bug ! */
	color: #000;
}
td .dragTarget {
	width: 90%;
}
/* THESE VALUES NEED TO BE ADJUSTED ON BOTH CORE AND 768 STYLESHEETS DEPENDENT ON TEXT SIZE */
.dragTarget, .categorise .dragItem {
	min-height: 21px;
	line-height: 1.5em;
}
.dragTarget {
	vertical-align: -7px;
	transition: background 300ms;
	-moz-transition: background 300ms;
	-webkit-transition: background 300ms;
}
.dragTarget.populated {
	vertical-align: baseline;
}
.dragTarget.inline.populated {
	display: inline;
	padding-top: 3px;
	padding-bottom: 2px;
	/* Top and bottom values are different because we are tyring to match the 21px total height (14px x 1.5em)*/
}
td .dragTarget.inline.populated, .blockSet .block .dragTarget.inline.populated {
	display: inline-block;
	padding-top: 0;
	padding-bottom: 0;
	/*In the core stylesheet, we are making the inline dragTargets inline-block to avoid the 'wrapping' effect. Not totally sure that this is a good idea, but it seems to have the desired effect*/
}
.dragTarget, .wordBox li.dragItem, .keyboardPlaceholder {
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	border-radius: 5px;
}
.keyboardPlaceholder {
    position: absolute;
    top: -10px;
    list-style: none;
}
/* ====================== */

/* Mobile Tap and drop Selected */
li.dragItem.selected, .dragTarget.selected, .wordBox li.dragItem.selected {
	background-color: #333;
	color: #FFF;
}

/* Desktop Item while dragged */
li.dragItem.ui-draggable-dragging, ul.ordering li.ui-sortable-helper, .ul.ordering li.ui-sortable-helper,
.rcfOrderingContainer.inlineOrdering .orderingItem.ui-sortable-helper {
	z-index: 1000;
	background-color: #FFF;
	-webkit-box-shadow: 2px 2px 3px rgba(50, 50, 50, 0.75);
	-moz-box-shadow: 2px 2px 3px rgba(50, 50, 50, 0.75);
	box-shadow: 2px 2px 3px rgba(50, 50, 50, 0.75);
}
/* Desktop target 'activated' */
.dragTarget.dragOverDrop {
	background-color: #333;
	color: #FFF;
}
/* Desktop droppable 'activated' */
li.dragItem.ui-draggable-dragging.dragOverDrop {
	background-color: #FFF;
	color: #333;
	border: 1px dashed #333;
}
/* IMAGE DRAGGING */


.blockSet .block .dragImageTarget img {
	margin: 0;
}
.dragImageItem, .wordBox li.dragImageItem, .dragImageTarget, .category li.dragImageItem {
	border-color: #000;
	padding: 2px;
	width: 50px;
	height: 50px;
}
.dragImageItem img, .wordBox li.dragImageItem img, .dragImageTarget img {
	width: 50px;
	height: 50px;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	border-radius: 5px;
}
.markable.droppableImage {
	display: inline-block;
}

.dragTarget.inline.populated.dragImageTarget {
	display: block;
}
.dragImageItem .caption, .dragImageTarget .caption {
	text-align: center;
	-webkit-border-bottom-right-radius: 5px;
	-webkit-border-bottom-left-radius: 5px;
	-moz-border-radius-bottomright: 5px;
	-moz-border-radius-bottomleft: 5px;
	border-bottom-right-radius: 5px;
	border-bottom-left-radius: 5px;
}

/* FIXED WORD POOL */
.fixedWordBox {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    box-sizing: border-box;
}
.itemBasedItem:not(.active) .fixedWordBox {
    display: none;
}
.panelsContainer  .fixedWordBox {
    bottom: 71px;
}
.viewerPopUp .fixedWordBox {
    bottom: 54px;
}
.panelsContainer.editPanelOpen .fixedWordBox {
    left: 45%;
}
.panelsContainer.tocOpen .fixedWordBox {
    left: 301px;
}
.panelsContainer.editPanelOpen.tocOpen .fixedWordBox {
    left: calc(((100% - 300px) / 100) * 45 + 300px);
}


/* DROPDOWN */

.content select {
	font-size: 1em;
	cursor: pointer;
	margin-right: 3px;
	margin-left: 3px;
}

/* dropdown itemSelect styles */
.activity[data-dropdownlistoutput="y"] select {
    border-color: transparent;
    border-left-width: 12px;
    border-right-width: 12px;
    outline: 1px solid #A5A5A6;
}
.ui-selectmenu-button {
    position: relative;
    background-color: #fff;
    border: 1px solid #000;
    border-radius: 5px;
    padding: 0.5em;
    white-space: nowrap;
    display: inline-block;
}
.ui-selectmenu-button.ui-selectmenu-button-open:not(.ui-selectmenu-menu-flipped) {
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
}
.ui-selectmenu-button.ui-selectmenu-button-open.ui-selectmenu-menu-flipped {
    border-top-right-radius: 0;
    border-top-left-radius: 0;
}
.ui-selectmenu-button .ui-selectmenu-icon {
    position: absolute;
    right: 4px;
    top: 0.5em;
    height: 22px;
    width: 22px;
    color: #000;
    background-image: url(../images/blendedFlashcards_nav_next.svg);
    background-size: 12px 12px;
    background-position: center;
    transform: rotate(90deg);
}
.ui-selectmenu-button.ui-selectmenu-button-open .ui-selectmenu-icon {
    transform: rotate(-90deg);
}
.ui-selectmenu-button .ui-selectmenu-text {
    padding-right: 1.5em;
}
.ui-selectmenu-menu {
    display: none;
    position: absolute;
    left: 0;
    bottom: 0;
}
.ui-selectmenu-menu.ui-selectmenu-open {
    display: inline-block;
    z-index: 250;
}
.ui-selectmenu-menu .ui-menu {
    border: 1px solid #000;
    background: #fff;
    padding: 0;
}
.ui-selectmenu-menu .ui-menu {
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
}
.ui-selectmenu-menu .ui-menu-item-wrapper {
    padding: 0.5em;
}
.ui-selectmenu-menu .ui-menu-item:not(:last-child) .ui-menu-item-wrapper {
    border-bottom: 1px solid #000;
}
.ui-selectmenu-menu .ui-menu-item-wrapper.ui-state-active {
    background-color: #000;
    color: #fff;
}
/* dropdown itemSelect focus styles */
.ui-selectmenu-button:focus ~ .ui-selectmenu-menu.ui-selectmenu-open .ui-menu {
    outline: 2px solid #000;
    outline-offset: -1px;
    z-index: 1;
}
/* dropdown itemSelect example styles */
.example .ui-selectmenu-button {
    color: #666;
    border-color: #666;
}
/* dropdown itemSelect disabled styles */
.ui-selectmenu-button.ui-state-disabled,
.example .ui-selectmenu-button.ui-state-disabled {
    color: #aaa;
    border-color: #aaa;
}
.ui-selectmenu-button.ui-state-disabled .ui-selectmenu-icon {
    opacity: 0.5;
}

/* for when the popup needs to be flipped / goes over the bottom of the activity */
.ui-selectmenu-menu.ui-front.ui-selectmenu-menu-flipped {
	bottom: auto;
}

/* TYPEIN */

.typein {
	white-space: nowrap;
}
.typein .hintText {
	white-space: normal;
}

span.markedTextInput.offscreenInput, input.text {
	-webkit-user-select: text; /* allow text input on iOS */
	color: #111;
	font-size: 1em;
	border: 1px solid #333;
	padding-left: 0.2em;
	padding-right: 0.2em;
	margin-left: 0.1em;
	margin-right: 0.1em;
	padding-top: 0;
	padding-bottom: 0;
	line-height: 1.5em;
	height: 1.5em; /* Hack for firefox - http://stackoverflow.com/questions/10274016/firefox-bug-with-input-line-height-any-work-around */
	background-color: #f8f8f8;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	border-radius: 5px;
	-moz-box-shadow: inset 1px 1px 2px #999;
	-webkit-box-shadow: inset 1px 1px 2px #999;
	box-shadow: inset 1px 1px 2px #999;

}

span.markedTextInput.offscreenInput {
	position: absolute;
	left: -3000px;
	top: -2000px;
	width: auto;
	display: inline-block;
}

table input.text.unmarkedText, li input.text.unmarkedText {
	width: 100%;
	box-sizing: border-box;
	-webkit-box-sizing:border-box;
	-moz-box-sizing: border-box;
}
table textarea {
	margin-bottom: 0;
}
.submitButton {
	visibility: hidden;
	height: 0;
	width: 0;
	padding: 0;
	margin: 0;
	position: absolute;
}
.hintText {
	font-weight: bold;
	font-style: italic;
}
input.checkbox, input.radio {
	margin-right: 0.8em;
}
.checkBoxTable input.checkbox, .radioTable input.radio {
	margin-right: 0;
