/* Ordering click & stick controls */
.orderingItem.selected {
    position: relative;
}
.orderingItem.selected .clickAndStickNavigation {
    position: absolute;
    width: 28px;
    height: 28px;
    top: -28px;
    background: #fff;
	color: #000;
    border: 1px solid #000;
	border-radius: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.orderingItem.selected .clickAndStickNavigation.disabled {
	background-color: rgba(200, 200, 200, 1);
	cursor: default;
}

.orderingItem.selected .clickAndStickNavigation.previousItem {
    right: calc(50% - 0.5px);
}
.orderingItem.selected .clickAndStickNavigation.nextItem {
    left: calc(50% - 0.5px);
}
.orderingItem.selected .clickAndStickNavigation svg {
	pointer-events: none;
	width: 100%;
	height: 100%;
}
.orderingItem.selected .clickAndStickNavigation svg path {
	fill: #333;
	stroke: #fff;
	stroke-width: 1;
}
.orderingItem.selected .clickAndStickNavigation.disabled svg path {
	fill: rgb(74, 84, 100);
	stroke: rgba(200, 200, 200, 1);
}
.ordering.horizontal .orderingItem.selected .clickAndStickNavigation.previousItem svg {
	transform: rotate(90deg);
}
.ordering.horizontal .orderingItem.selected .clickAndStickNavigation.nextItem svg {
	transform: rotate(-90deg);
}
.ordering.vertical .orderingItem.selected .clickAndStickNavigation.previousItem svg {
	transform: rotate(180deg);
}
.orderingItem.selected .clickAndStickNavigation.active {
    background: #333;
}
.orderingItem.selected .clickAndStickNavigation.active svg path {
	stroke: #333;
	fill: #fff;
}

