	display: none;
}

/* FIND IN IMAGE */

.findInImage {
	position: relative;
}

.findInImage .stagingScreen {
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	position: absolute;
	background: purple;
	display: none;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.findInImage .findInImageStartScreen {
	display: flex;
	top: initial;
	left: 0;
	height: 50px;
	background: transparent;
}

.findInImage:not(.playing) .findInImageContainer {
    pointer-events: none;
}

.findInImage.playing .stagingScreen {
	display: none;
}

.findInImage.gameOver .findInImageStartScreen {
	display: none;
}

.findInImage.gameOver .findInImageGameOverScreen {
	display: flex;
	background: red;
}

.findInImage.givingFeedback:after {
	display: block;
    content: ' ';
    left :0;
    top: 0;
    height: 100%;
    width: 100%;
    position: absolute;
    opacity: 0.5;
    cursor: default;
}

.findInImage g.clickableElement {
	cursor: pointer;
}

.findInImage svg .selected ellipse,
.findInImage svg .correctAnswer ellipse,
.findInImage svg .incorrectAnswer ellipse {
	stroke-width: 8px;
}

.findInImageContainer svg .selected ellipse {
    stroke: #FFC502;
}

.findInImageContainer svg .correctAnswer ellipse {
    stroke: green;
}

.findInImageContainer svg .incorrectAnswer ellipse {
    stroke: red;
}

.findInImage .dev-prompts .findInImageQuestionPrompt {
	display: none;
}

.findInImage.playing .findInImageQuestionPrompt.active {
	display: initial;
}

