/* SLIDING BLOCK */
.slidingBlock {
	position: fixed;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	visibility: hidden;
	-webkit-transition: visibility 0s 0.6s;
	-moz-transition: visibility 0s 0.6s;
	transition: visibility 0s 0.6s;
}
/* overlay layer */
.slidingBlock:after {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: transparent;
	cursor: pointer;
	-webkit-transition: background 0.3s 0.3s;
	-moz-transition: background 0.3s 0.3s;
	transition: background 0.3s 0.3s;
}
.slidingBlock.slidingOpen {
	visibility: visible;
	-webkit-transition: visibility 0s 0s;
	-moz-transition: visibility 0s 0s;
	transition: visibility 0s 0s;
}
.slidingBlock.slidingOpen:after {
	background: rgba(0, 0, 0, 0.6);
	-webkit-transition: background 0.3s 0s;
	-moz-transition: background 0.3s 0s;
	transition: background 0.3s 0s;
}


/* SLIDING BLOCK: HEADER */
.slidingHeader {
	position: fixed;
	width: 90%;
	height: 50px;
	line-height: 50px;
	background-color : #E4E4E4;
	padding: 5px 0.875em;
	z-index: 9101;
}


/* SLIDING BLOCK: CLOSE */
.slidingButtonClose:after,
.slidingButtonClose:before {
	content: none;
}
.slidingButtonClose {
	color: #666;
	position: absolute;
	top: 0;
	right: 0;
	height: 100%;
	width: 60px;
	cursor: pointer;
}
.slidingButtonClose:hover {
	color: #437082;
}



/* SLIDING BLOCK: PANEL */
.slidingPanel {
	position: fixed;
	width: 90%;
	height: 100%;
	background: #FFF;
	z-index: 9100;
}


/* SLIDING BLOCK: TRANSITION */
.slidingHeader,
.slidingPanel {
	top: 0;
	-webkit-transition-property: -webkit-transform;
	-moz-transition-property: -moz-transform;
	transition-property: transform;
	-webkit-transition-duration: 0.3s;
	-moz-transition-duration: 0.3s;
	transition-duration: 0.3s;
	-webkit-transition-delay: 0.3s;
	-moz-transition-delay: 0.3s;
	transition-delay: 0.3s;
}
.from-right .slidingHeader,
.from-right .slidingPanel {
	right: 0;
	-webkit-transform: translate3d(100%, 0, 0);
	-moz-transform: translate3d(100%, 0, 0);
	-ms-transform: translate3d(100%, 0, 0);
	-o-transform: translate3d(100%, 0, 0);
	transform: translate3d(100%, 0, 0);
}
.from-left .slidingHeader,
.from-left .slidingPanel {
	left: 0;
	-webkit-transform: translate3d(-100%, 0, 0);
	-moz-transform: translate3d(-100%, 0, 0);
	-ms-transform: translate3d(-100%, 0, 0);
	-o-transform: translate3d(-100%, 0, 0);
	transform: translate3d(-100%, 0, 0);
}
.slidingOpen .slidingHeader,
.slidingOpen .slidingPanel {
	-webkit-transform: translate3d(0, 0, 0);
	-moz-transform: translate3d(0, 0, 0);
	-ms-transform: translate3d(0, 0, 0);
	-o-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
	-webkit-transition-delay: 0s;
	-moz-transition-delay: 0s;
	transition-delay: 0s;
}

/* SLIDING BLOCK: CONTENT */
.slidingContent {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	padding: 0.875em;
	padding-top: 70px;
	overflow: auto;
