/* STORY */
.story {
  position: relative;
}

.cover,
.scene {
  position: absolute;
  top: 0;
  left: 0;
}

.frame .speech {
  position: absolute;
  background: #FFF;
  margin: 0 5px 0 0;
}
.frame .speech p {
  position: relative;
  margin: 0;
  padding: 10px 20px;
}

.frame [class*='tip'].speech p:after {
  content: "";
  position: absolute;
  display: block;
  width: 0;
  border-style: solid;
}

.frame [class*='tipTop'].speech p:after {
  top: -10px;
  left: calc(50% - 10px);
  border-width: 0 10px 10px;
  border-color: #FFF transparent;
}
.frame .speech.tipTopLeft p:after {
  left: 10px;
}
.frame .speech.tipTopRight p:after {
  left: auto;
  right: 10px;
}
.frame [class*='tipRight'].speech p:after {
  top: calc(50% - 10px);
  right: -10px;
  border-width: 10px 0 10px 10px;
  border-color: transparent #FFF;
}
.frame .speech.tipRightTop p:after {
  top: 10px;
}
.frame .speech.tipRightBottom p:after {
  top: auto;
  bottom: 10px;
}
.frame [class*='tipBottom'].speech p:after {
  bottom: -10px;
  left: calc(50% - 10px);
  border-width: 10px 10px 0;
  border-color: #FFF transparent;
}
.frame .speech.tipBottomLeft p:after {
  left: 10px;
}
.frame .speech.tipBottomRight p:after {
  left: auto;
  right: 10px;
}
.frame [class*='tipLeft'].speech p:after {
  top: calc(50% - 10px);
  left: -10px;
  border-width: 10px 10px 10px 0;
  border-color: transparent #FFF;
}
.frame .speech.tipLeftTop p:after {
  top: 10px;
}
.frame .speech.tipLeftBottom p:after {
  top: auto;
  bottom: 10px;
}

/* Story: modes */
.story .cover {
  position: relative;
  display: inline-block;
}
.story .cover .modes {
  position: absolute;
  bottom: 1em;
  text-align: center;
  width: 100%;
}
.story .modes ul {
  position: relative;
  display: inline-block;
  background-color: #FFF;
  text-align: center;
  padding: 5px;
}

.story .modes .button {
  display: none;
  padding: 5px;
  border: 1px solid #000;
}
.story .modes .button.active {
  display: inline-block;
}
/* hide speech bubbles in listen only mode */
.story.modeListen .speech {
  display: none;
}
/* sequence */
.frame .speech {
  display: none; /* hidden by default */
}
.frame .speech.active {
  display: block;
}

.cover.hide .modes .button {
	display: none;
}

/* Story: transitions */
.cover, .scene {
  opacity: 1;
	z-index: 999;
  transition: opacity .25s ease-in-out;
  -moz-transition: opacity .25s ease-in-out;
  -webkit-transition: opacity .25s ease-in-out;
}
.cover.hide,
.scene.hide {
  opacity: 0;
	z-index: 1;
}
/* Story: comprehension questions */
.scene.comprehension {
  background-color: #FFF;
    height: 100%;
}
.scene.comprehension .questions {
  display: none;
}
.scene.showQuestions .frame {
    -ms-transform: scale(0.75, 0.75);
    -webkit-transform: scale(0.75, 0.75);
    transform: scale(0.75, 0.75);
    -ms-transform-origin: 50% 0;
    -webkit-transform-origin: 50% 0;
    transform-origin: 50% 0;
}
.scene.showQuestions .questions {
  display: block;
  margin-top: -175.5px; /* 702 - (702 * 0.75) */
}
/* Story: navigation */
.story .navigation {
  position: absolute;
  bottom: 1em;
  right: 1em;
	z-index: 1000;
}
.story .navigation ul {
  position: relative;
  display: inline-block;
  background-color: transparent;
  text-align: center;
  padding: 5px;
}
.story .navigation .button {
  display: none;
  padding: 5px;
  border: 1px solid #000;
}
.story .navigation .button.active {
  display: inline-block;
}
.story .navigation ul .button:not(.active) {
    pointer-events: none;
}

