/*tileMaze LIVES*/

/* only show tileMaze lives when game is playing, or gameWon/Over screen is shown */
.tileMazeContainer .tileMazeStageContainer .lives {
	display: none;
}

.tileMazeContainer.playing .tileMazeStageContainer .lives,
.tileMazeContainer.gameWon .tileMazeStageContainer .lives,
.tileMazeContainer.gameOver .tileMazeStageContainer .lives,
.tileMazeContainer.nextQuestion .tileMazeStageContainer .lives {
	display: flex;
}

.tileMazeContainer .tileMazeStageContainer .lives {
	position: absolute;
	right: 10px;
	padding: 0;
	flex-wrap: wrap;
}

.tileMazeContainer .tileMazeStageContainer .lives .life {
	/* eventually this image can be set in code - all spritesheets *should* have same keys / positions ! */
	background: url(../images/tileMazeAssets/spritesheet.png) 0px 1px no-repeat;
	height: 40px;
	width: 40px;
	min-width: 40px;
	min-height: 40px;
	flex-grow: 1;
	flex-shrink: 1;
}

.tileMazeStageContainer .lives .life.dead {
	animation: 500ms tileMazeLifeLost;
	animation-fill-mode: forwards;
}

/* tiny animation to draw user to the lives / life lost in tilemaze */
@keyframes tileMazeLifeLost {
	0%, 99% {
		transform: scale(0.95);
	}
	50% {
		transform: scale(1.3);
	}
	99.99% {
		background-position: 0px 1px;
	}
	100% {
		background-position: -40px 1px;
	}
}

