/* MATHS INTERACTION */

/* MATHS ADDITION */
.mathsInteraction {
    display: flex;
    font-family: arial;
    padding: 0.5em;
    background: white;
}

.mathsInteraction .marks-container {
    position: relative;
}

.rcfMathsTable {
	display: table;
	border-collapse: collapse;

    background: #fff;
	color: #333;
	border-collapse: collapse;
    margin-bottom: 0;
}

.rcfMathsTable .cell {
	border: 1px solid #eee;
	padding: 0;
	text-align: center;
	vertical-align: middle;
	height: 20px;
	min-height: 20px;
}

.rcfMathsTable td.cell.symbol {
    vertical-align: middle;
    font-size: 1.6em;
}
.rcfMathsTable .cell.answerSymbol {
	color: transparent;
}

.rcfMathsTable.hasDecimals .cell.spacer,
.rcfMathsTable.hasDecimals .cell.decimal-point {
    max-width: 5px;
    min-width: 5px;
}

.rcfMathsTable:not(.hasDecimals) .cell.spacer {
    display: none;
}

.rcfMathsTable:not(.hasDecimals) .answerRow td {
    padding: 0.2em;
}


.rcfMathsTable tr.workingRow td.cell {
    padding-top: 0.1em;
    padding-bottom: 0.1em;
}

.rcfMathsTable tr.workingRow.firstWorkingRow td,
.rcfMathsTable tr.answerRow td {
    border-top: 2px solid #f7b02a;
    padding-top: 0.2em;
	padding-bottom: 0.2em;
}

.rcfMathsTable tr.workingRow:not(.firstWorkingRow) td {
	border-top: 1px solid #fff;
}

.rcfMathsTable tr.workingRow.lastWorkingRow td {
    padding-bottom: 0.2em;
}


.rcfMathsTable .decimal-point {
	max-width: 10px;
	padding: 0;
	min-width: 10px;
}

.rcfMathsTable input.answer-input,
.rcfMathsTable .workingRow input.working-answer-input {
	text-align: center;
	font-weight: normal;
	font-size: larger;
	border-radius: 5px;
	min-height: 1.5em;
	min-width: 1.2em;
	background-color: #d3f0f4;
	border: 1px solid #2cb3c5;
	padding: 0;
}

.rcfMathsTable .correct.answerRow input.answer-input:not(.decimal-point) {
	background-color: green;
	border: 1px solid darkgreen;
	color: white;
}

.rcfMathsTable .incorrect.answerRow input.answer-input:not(.decimal-point) {
	background-color: red;
	border: 1px solid darkred;
	color: white;
}

.rcfMathsTable input.answer-input.decimal-point {
	background-color: transparent;
	border: none;
	max-width: 5px;
	min-width: 5px;
}

.rcfMathsTable tr td.cell.workingCell {
	padding: 0;
}

.rcfMathsTable .workingRow .cell .working-answer-input.hidden {
	display: none;
}

/* marking / working area feedback */
.dev-markable-container.incorrectAnswer .rcfMathsTable .answerRow,
.dev-markable-container.incorrectAnswer .rcfMathsTable .answerRow td {
	background-color: salmon;
	border-left-color: salmon;
	border-right-color: salmon;
}

.marking .rcfMathsTable tr.workingRow.firstWorkingRow:has(input.valid,input.invalid) td {
	border-top: 2px solid #f7b02a ;
}

 .marking .rcfMathsTable tr.workingRow:has(input.valid) {
	background-color: lightgreen;
 }

 .marking .rcfMathsTable tr.workingRow:has(input.valid) td {
	border-color: lightgreen;
 }

 .marking .rcfMathsTable tr.workingRow:has(input.invalid) {
	background-color: salmon;
 }

 .marking .rcfMathsTable tr.workingRow:has(input.invalid) td {
	border-color: salmon;
 }

 .marking .rcfMathsTable tr.workingRow input.invalid {
	background-color: red;
	border-color: red;
	outline: 2px solid #333;
	color: white;
 }

/* DIVISION */

/* DIVISION STYLES */

/* english layout styles for divisor / dividend */
.division.layout-english table.rcfMathsTable tr.answerRow td {
	border-top: 1px solid #eee;
}

.division.layout-english table.rcfMathsTable tr.answerRow td:nth-of-type(1) {
	border-right: none;
	border-bottom: none;
}

.division.layout-english table.rcfMathsTable tr.answerRow td:nth-of-type(2) {
	border-left: none;
}

.division.layout-english table.rcfMathsTable tr.sumRow td.cell {
	border-top: 2px solid black;
}

.division.layout-english table.rcfMathsTable tr.sumRow td.cell:not(:last-child) {
	border-right: none;
}

.division.layout-english table.rcfMathsTable tr.sumRow td.divisor.cell {
	border-right: 2px solid black;
	border-top: 1px solid transparent;
	min-width: 25px;
	letter-spacing: 0.2em;
	padding: 0.2em;
}


/* general division styles for working-out */
.division .marks-container  {
	top: -100%;
}

/* the 'underlined' total cells in the working area */
.division table.rcfMathsTable tr.productRow td.hasInput,
.division table.rcfMathsTable tr.productRow td.productCell,
.division table.rcfMathsTable tr.productRow td.productCell + td.spacer:has(+ td.productCell),
.division table.rcfMathsTable tr.productRow td.productCell + td.spacer:has(+ td.hasInput),
.division table.rcfMathsTable tr.productRow td.hasInput + td.spacer:has(+ td.hasInput) {
	border-bottom: 2px solid black;
}

/* remainders */
.division table.rcfMathsTable tr.workingRow.remainderRow td {
	padding-top: 5px;
}

/* output a 'R' in the cell adjacent to a marked remainder cell - cheating? maybe :) */
.division[data-rcfDecimalPlaces="0"] table.rcfMathsTable tr.sumRemainderRow td.cell:not(.hasInput):has(+ td.cell.spacer + td.cell.hasInput):after {
	content: 'R';
}

/* hide working rows (excluding sumRemainderRow) if showWorking=n and markRemainder=y in the xml */
.division.onlyShowRemainderWorking table.rcfMathsTable tr.workingRow:not(.sumRemainderRow) {
    display: none;
}

