.mathsInteraction.division.layout-european .dev-european-layout .rcfMathsTable tr:nth-child(2) td {
	border-top: none;
}

.mathsInteraction.division.layout-european .dev-european-layout .rcfMathsTable.rcfDivisionEuropeanAnswerTable tr:nth-child(2) td {
