/* RCF-11575 - colourText */
/* Base styles */
mark.colourText {
	background-color: inherit;
	color: var(--CHRcolouredText-color,#9900CC);
}

/* Screen-reader styles */
mark.colourText::before, mark.colourText::after {
	clip-path: inset(100%);
	clip: rect(1px, 1px, 1px, 1px);
	height: 1px;
	width: 1px;
	overflow: hidden;
	position: absolute;
	white-space: nowrap;
}

mark.colourText::before {
	content: " [colour text start] ";
}

mark.colourText::after {
	content: " [colour text end] ";
}

/* Screen-reader styles in another language */
*[lang=es] mark.colourText::before {
	content: " [inicio de texto coloreado] ";
}

*[lang=es] mark.colourText::after {
	content: " [final de texto coloreado] ";
}

/* RCF-11616 - description list element */
dl {
  margin-top: 1em;
  margin-bottom: 1em;
  margin-left: 0;
  margin-right: 0;
}

dd {
  margin-left: 40px;
}
